iframe {
  display: none;
}

button {
  cursor: pointer;
}

body,
h1 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background-color: #f1f1f1;

  h1 {
    font-size: 32px;
    color: #333;
    margin-bottom: 10px;
    text-transform: uppercase;
  }

  button {
    width: 150px;
    color: #fff;
    background-color: #333;
    border: none;
    padding: 10px 20px;
    margin-bottom: 5px;
    border-radius: 3px;

    &:hover {
      background-color: #555;
    }
  }
}
